(function() {
  'use strict';

  angular
    .module('nportoCom', ['ngAnimate', 'ngTouch', 'ngSanitize','ui.router','ngOnload','np.github-contributions'])
    .config(config)
    .constant('CONFIG',{});

  /** @ngInject */
  function config($logProvider, $locationProvider) {
    
    $locationProvider.html5Mode(false); // for removing hash from the url
    
    // Enable log
    $logProvider.debugEnabled(true);
  }

})();

/*global GitHubCalendar,angular*/
(function() {
    'use strict';

    angular
        .module('nportoCom')
        .controller('MainController', MainController);

    /** @ngInject */
    function MainController($scope, $timeout, $location, $rootScope, $anchorScroll, $http) {


        //load cv from json 
        $http.get('resume/en/resume.json', {
            cache: true
        }).then(function(res) {
            console.log(res.data);
            $scope.cv = res.data;
        });
        
        $rootScope.colors = "css/blue.css";
        $scope.changeColor = function(e) {
            $rootScope.colors = e;
        }
        $rootScope.menu = false;
        $scope.dataLoad = function() {
            $scope.loader = true;
            $timeout(function() {
                $scope.loader = false;

            }, 600);
        }
        $scope.closeMenu = function() {
            leftSide.addClass('closeMobileMenu')
            leftSide.removeClass('openMobileMenu')
        }

        var loads = angular.element(document.querySelector('.loads'));
        $scope.loader = true;
        $timeout(function() {
            loads.css({
                'height': '220px',
                'z-index': 0,
                'opacity': 0
            })
            $scope.loader = false;
        }, 600)

        var leftSide = angular.element(document.querySelector('.left-side'));
        $scope.openMenu = function() {
            leftSide.addClass('openMobileMenu')
            leftSide.removeClass('closeMobileMenu')
        }

        $scope.closeItem = function() {
            $rootScope.menu = false;
            $location.path('/').hash('')
        }
        $rootScope.blog = true;
        $scope.itemone = false;
        $timeout(function() {
            $scope.itemone = true;
        }, 250);
        $scope.itemtwo = false;
        $timeout(function() {
            $scope.itemtwo = true;
        }, 450);
        $scope.loadCont = function(e) {

            $location.path('/').hash('')
            $scope.loader = true;
            $timeout(function() {
                $scope.loader = false;
            }, 600);
            $rootScope.menu = false;
            $rootScope.views = e;
            $scope.itemone = false;
            $timeout(function() {
                $scope.itemone = true;
            }, 250);
            $scope.itemtwo = false;
            $timeout(function() {
                $scope.itemtwo = true;
            }, 450);
            // $scope.$apply();
            console.log(e)
        }
    };

})();

(function() {
  'use strict';

  var app=angular
    .module('nportoCom');
    

    /*********
     *** full-width-container directive ***
     *********/
    app.directive('fullWidthContainer', function() {
        return {
            restrict: 'C',
            transclude: true,
            template: '<div class="single-info-container slide-top" ng-if="itemone" ng-transclude></div>'
        }
    });

    /*********
     *** left-side-container directive ***
     *********/
    app.directive('leftSideContainer', function() {
        return {
            restrict: 'C',
            transclude: true,
            template: '<div class="col-md-8 slide-top" ng-if="itemone" ng-transclude></div>'
                // template: '<div class="info-container slide-top" ng-if="itemone" ng-transclude></div>'
        }
    });

    /*********
     *** right-side-container directive ***
     *********/
    app.directive('rightSideContainer', function() {
        return {
            restrict: 'C',
            transclude: true,
            template: '<div class="col-md-4 slide-top" ng-if="itemtwo" ng-transclude></div>'
                // template: '<div class="info-container-two slide-top" ng-if="itemtwo" ng-transclude></div>'
        }
    });

    /*********
     *** fab-close button directive ***
     *********/
    app.directive('fabClose', ['$timeout', function($timeout) {
        return {
            restrict: 'C',
            template: '<div class="accent close-button slide-top" ng-click="closeItem()" ng-if="fabBtn"></div>',
            link: function(scope, element, attribute) {
                scope.fabBtn = false;
                $timeout(function() {
                    return scope.fabBtn = true;
                }, 650);
            }
        }
    }]);

 
    /*********
     *** info-items directive ***
     *********/
    app.directive('infoItems', ['$rootScope', '$timeout', function($rootScope, $timeout) {
        return {
            restrict: 'C',
            transclude: true,
            template: '<ng-transclude ng-click="dataLoad()"></ng-transclude>',
            link: function(scope, element, attribute) {
                element.on('click', function() {
                    $timeout(function() {
                        $rootScope.loadDatas = attribute['path'];
                    }, 200);
                })
            }
        }
    }]);

    /*********
     *** nav-home directive ***
     *********/
    app.directive('navHome', ['$rootScope', function($rootScope) {
        return {
            restrict: 'C',
            link: function(scope, element, attribute) {
                $rootScope.views = attribute['path'];
            }
        }
    }]);


    // /*********
    //  *** load data directive ***
    //  *********/
    // app.directive('loadData', ['$rootScope', '$timeout', '$location', function($rootScope, $timeout, $location) {
    //     return {
    //         restrict: 'C',
    //         transclude: true,
    //         template: '<div ng-click="closeMenu()" ng-transclude></div>',
    //         link: function(scope, element, attribute) {
    //             element.on('click', function() {
    //                 $location.path('/').hash('')
    //                 scope.loader = true;
    //                 $timeout(function() {
    //                     scope.loader = false;
    //                 }, 600);
    //                 $rootScope.menu = false;
    //                 $rootScope.views = attribute['path'];
    //                 scope.itemone = false;
    //                 $timeout(function() {
    //                     scope.itemone = true;
    //                 }, 250);
    //                 scope.itemtwo = false;
    //                 $timeout(function() {
    //                     scope.itemtwo = true;
    //                 }, 450);
    //                 scope.$apply();
    //             })
    //         }
    //     }
    // }]);

    
})();

(function() {
  'use strict';

  angular
    .module('nportoCom')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      // .state('cv', {
      //   url: '/cv',
      //   templateUrl: 'resume/en/index.html',
      //   controller: function(){
          
      //   }
      // })
      .state('home', {
        url: '',
        abstract:true,
        templateUrl: 'app/main/main.html',
        controller: 'MainController'
        // controllerAs: 'main'
      })
      .state('home.about', {
        url: '/',
        templateUrl: 'app/about.html'
      })
      .state('home.techstack', {
        url: '/techstack',
        templateUrl: 'app/techstack.html'
      })
      .state('home.work', {
        url: '/work',
        templateUrl: 'app/work.html'
      })
      .state('home.projects', {
        url: '/projects',
        templateUrl: 'app/projects.html',
        controller: 'MainController'
      })
      .state('home.projects.single', {
        url: '/test',
        templateUrl: 'app/single-portfolio.html'
      })
      .state('home.demos', {
        url: '/demos',
        templateUrl: 'app/demos.html'
      })
      .state('home.academic', {
        url: '/academic',
        templateUrl: 'app/academic.html'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

